export default {
	data() {
		return {
			news: []
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...'
		}),
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				//console.log(res.data)
				this.news=res.data
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openInfo(e){
			//console.log(e)
			var newsid =e.currentTarget.dataset.newsid
			//console.log(newid)
			uni.navigateTo({
				url: '../info/info?newsid='+newsid
			});
		}
	}
}
